import React from 'react'

class TodoList extends React.Component{

    state = {
        data : [],
    }

    static getDerivedStateFromProps(nextProps, prevState) {
        const temp = [...nextProps.data].reverse()
        if(temp.length >= 0){
            return {
                data: temp,
            }
        }else{
            return prevState
        }
        
       }


    render(){
        return( 
            <div className="row justify-content-center mt-3">
                <div className="col-md-8 col-sm-12">
                    <ul className="list-group">
                        {this.state.data.length > 0 ? this.state.data.map((data)=>{
                            return(
                                <li className="list-group-item d-flex justify-content-between" key={data._id}>
                                    <p className="lead">{data.todo}</p> 
                                    <div>
                                        <button className="btn btn-primary m-2 btn-sm" onClick={()=>this.props.handleEdit(data._id , data.todo)}>Edit</button>
                                        <button onClick={()=>this.props.handleDel(data._id)} className="btn btn-danger btn-sm">Delete</button>
                                    </div>
                                </li>
                            )
                        }):null}
                    </ul>
                </div>
            </div>
        )
    }
}

export default TodoList;