import React from 'react';
import axios from 'axios'
import TodoList from './TodoList'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'

const TodoSwal = withReactContent(Swal)

const ipAddress = 'localhost'

class App extends React.Component {
  
  state = {
    value : '',
    tododata : []
  }

  componentDidMount(){
    axios.get(`http://${ipAddress}:4000/get_todo`).then((res)=>{
    this.setState({
      tododata : res.data
    })
  })
}


  handleChange = (e) =>{
    this.setState({
      value : e.target.value
    })
  }

  handleSubmit = (e) =>{
    e.preventDefault()

    const body = {
      todo : this.state.value.trim()
    }
    
    axios.post(`http://${ipAddress}:4000/add_todo`, body).then(()=>{
      axios.get(`http://${ipAddress}:4000/get_todo`).then((res)=>{
        this.setState({
          value : '',
          tododata : res.data
        })
      })
    }).then(()=>{
      const Toast = Swal.mixin({
        toast: true,
        position: 'top-end',
        showConfirmButton: false,
        timer: 3000
      })
      
      Toast.fire({
        type: 'success',
        title: 'Todo Added Successfully'
      })
    })
  }

  handleDel = (id) =>{

    TodoSwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      type: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes, delete it!'
    }).then((result) => {
      if (result.value) {
        axios.delete(`http://${ipAddress}:4000/delete_todo/${id}`).then(()=>{
          axios.get(`http://${ipAddress}:4000/get_todo`).then((res)=>{
              this.setState({
                tododata : res.data
              })
          })
      }).then(()=>{
        const Toast = Swal.mixin({
          toast: true,
          position: 'top-end',
          showConfirmButton: false,
          timer: 3000
        })
        
        Toast.fire({
          type: 'success',
          title: 'Todo Deleted Successfully'
        })
      })
        
      }
    })

}


handleEdit = (id, value) => {
  TodoSwal.fire({
    text: `You Want to Update "${value}" ?`,
    type : 'question',
    input : 'text',
    inputAutoTrim : true,
    inputPlaceholder : 'Enter Updated Value',

    allowEnterKey : true,
    showCancelButton: true,
    confirmButtonColor: '#3085d6',
    cancelButtonColor: '#d33',
    confirmButtonText: 'Yes, Update it!'

  }).then((result)=>{
    if(result.value){
      const body = {
        todo : result.value
      }

      axios.put(`http://${ipAddress}:4000/update_todo/${id}`, body).then(()=>{
        axios.get(`http://${ipAddress}:4000/get_todo`).then((res)=>{
              this.setState({
                tododata : res.data
              })
          })
      }).then(()=>{
        const Toast = Swal.mixin({
          toast: true,
          position: 'top-end',
          showConfirmButton: false,
          timer: 3000
        })
        
        Toast.fire({
          type: 'success',
          title: 'Todo Updated Successfully'
        })
      })


    }
  })
}

  render(){
    return (
      <React.Fragment>
        <h3 className="text-center mt-2 h1">Todo List</h3>
        <div className="row justify-content-center mt-2">
        <div className="mr-3 w-50">
          <input type="text" value={this.state.value} className="form-control" onChange={this.handleChange}/>
        </div>
        <div>
        <button className="btn btn-primary" onClick={this.handleSubmit}>Add Todo</button>
        </div>
      </div>
      <TodoList data={[...this.state.tododata]} handleDel = {this.handleDel} handleEdit = {this.handleEdit} />
      </React.Fragment>

    )
  }
}

export default App;
